@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserratregular.woff2") format("woff2"), url("../fonts/montserratregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserratmedium.woff2") format("woff2"), url("../fonts/montserratmedium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserratsemibold.woff2") format("woff2"), url("../fonts/montserratsemibold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserratbold.woff2") format("woff2"), url("../fonts/montserratbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #011c40;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f9fbfd;
  -webkit-text-fill-color: #011c40;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  display: inline-flex;
  padding: 14px 20px;
  vertical-align: middle;
  color: #011c40;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

main {
  background-color: #002a5a;
}

.header {
  position: absolute;
  width: 100%;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 500;
  line-height: 20px;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .header {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .header {
    position: relative;
    font-size: 18px;
    line-height: 22px;
    background-color: #002a5a;
  }
  .header--JS-menu {
    position: absolute;
    background-color: transparent;
  }
}

.header-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 35px auto;
}
@media (max-width: 1023px) {
  .header-wrapper {
    margin: 30px auto;
  }
}
@media (max-width: 767px) {
  .header-wrapper {
    flex-direction: column;
    align-items: flex-start;
    margin: 20px auto;
  }
  .header-wrapper--close nav {
    display: none;
  }
  .header-wrapper--open {
    position: absolute;
    top: 0;
    right: 0;
    width: 285px;
    height: 100vh;
    justify-content: flex-start;
    margin-top: 0;
    padding: 20px 15px;
    color: #011c40;
    background-color: #f9fbfd;
  }
}

.header__logo-link {
  color: inherit;
  line-height: 0;
}
@media (max-width: 1023px) {
  .header__logo-link svg {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .header__logo-link {
    margin-bottom: 43px;
  }
  .header__logo-link svg {
    width: 42px;
    height: 22px;
  }
}

.header__menu-toggle {
  display: none;
  position: absolute;
  top: 10px;
  right: 0;
  width: 54px;
  height: 42px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media (max-width: 767px) {
  .header__menu-toggle--JS-menu {
    display: block;
  }
}

.header__menu-toggle-burger {
  display: block;
  box-sizing: border-box;
  position: relative;
  left: 15px;
  width: 24px;
  height: 2px;
  background-color: #f9fbfd;
  transition-duration: 0.25s;
  transition-delay: 0.25s;
}
.header__menu-toggle-burger::after, .header__menu-toggle-burger::before {
  content: "";
  position: absolute;
  width: 24px;
  height: 2px;
  left: 0;
  background-color: #f9fbfd;
  transition: transform 0.25s, top 0.25s 0.25s;
  transition-duration: 0.25s;
}
.header__menu-toggle-burger::after {
  top: -6px;
}
.header__menu-toggle-burger::before {
  top: 6px;
}
.header__menu-toggle-burger--close {
  transition-duration: 0.1s;
  transition-delay: 0.25s;
  background: transparent;
}
.header__menu-toggle-burger--close::after {
  transition: top 0.4s, transform 0.25s 0.25s;
  top: 0;
  transform: rotateZ(45deg);
  background-color: #011c40;
}
.header__menu-toggle-burger--close::before {
  transition: top 0.25s, transform 0.25s 0.25s;
  top: 0;
  transform: rotateZ(-45deg);
  background-color: #011c40;
}

.header__nav-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .header__nav-list {
    flex-direction: column;
  }
}

.header__nav-item {
  margin-left: 33px;
}
@media (max-width: 1023px) {
  .header__nav-item {
    margin-left: 23px;
  }
}
@media (max-width: 767px) {
  .header__nav-item {
    margin: 0;
  }
  .header__nav-item:not(:last-child) {
    margin-bottom: 28px;
  }
}

.header__link {
  color: inherit;
}
.header__link:hover {
  opacity: 0.8;
}

.footer {
  color: #f9fbfd;
  background-color: #011c40;
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 49px auto;
}
@media (max-width: 1023px) {
  .footer__wrapper {
    margin: 47px auto;
  }
}
@media (max-width: 767px) {
  .footer__wrapper {
    flex-direction: column;
    align-items: normal;
    margin: 40px auto;
  }
}

.footer__nav-wrapper {
  display: flex;
  align-items: center;
}
@media (max-width: 767px) {
  .footer__nav-wrapper {
    flex-direction: column;
    align-items: normal;
  }
}

.footer__logo-link {
  margin-right: 40px;
  color: inherit;
  line-height: 0;
}
@media (max-width: 1023px) {
  .footer__logo-link {
    margin-right: 25px;
  }
  .footer__logo-link svg {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .footer__logo-link {
    margin: 0 0 30px;
  }
  .footer__logo-link svg {
    width: 42px;
    height: 22px;
  }
}

.footer__nav-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .footer__nav-list {
    flex-direction: column;
  }
}

.footer__nav-item {
  margin-right: 33px;
  padding: 10px 0;
  line-height: 0;
}
@media (max-width: 1023px) {
  .footer__nav-item {
    padding: 2px 0;
  }
}
@media (max-width: 767px) {
  .footer__nav-item {
    margin: 0 0 23px;
    padding: 0;
  }
}

.footer__link {
  font-weight: 500;
  line-height: 20px;
  color: inherit;
}
@media (max-width: 1023px) {
  .footer__link {
    font-size: 12px;
    line-height: 16px;
  }
}
.footer__link:hover {
  opacity: 0.8;
}

.social {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.social__link {
  margin-left: 27px;
  padding: 10px 0;
  line-height: 0;
}
@media (max-width: 1023px) {
  .social__link {
    margin-left: 22px;
    padding: 5px 0;
  }
  .social__link svg {
    width: 16px;
    height: 16px;
  }
}
@media (max-width: 767px) {
  .social__link {
    margin-left: 0;
    margin-right: 22px;
  }
}

